.Container {
    display: flex;
    justify-content: center;
}

.LogoContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
}

.Logo {
    margin-right: 15px;
    margin-bottom: 35px;
}

.Title {
    font-family: 'Poppins';
    font-weight: 700;
    font-size: 22px;
    line-height: 0.85;
    color: #000000;
}

.Form {
    height: 100vh;
    max-width: 480px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    position: relative;
    justify-content: center;
    align-items: center;
    background-color: rgb(255, 255, 255);
}

.Field{
    position: relative;
    margin-bottom: 40px;
}
.ErrorMessage{
    padding: 0;
    margin: 0;
    font-size: 10px;
    width: inherit;
    color: red;
}

.InputIcon{
    width: 24px;
    height: 24px;
    position: absolute;
    left: 10px;
    top: 25%;
}

.Input{
        font-family: 'Circe';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 1.5;
        color: #bdbdbd;
        border: none;
        outline: none;
        border-bottom: 1px solid #e0e0e0;
        height: 24px;
        padding: 8px 8px 8px 45px;
}

.Input:focus{
    border-bottom: 1px solid #24cca7;
}

.PasswordVisibilityToggle{
    width: 30px;
    height: 30px;
    position: absolute;
    right: -5px;
    top: 30%;
    cursor: pointer;
}

.ButtonContainer{
    display: flex;
    flex-direction: column;
}

.ButtonPrimary{
    font-family: 'Circe';
    font-weight: 400;
    font-size: 18px;
    line-height: 1.5;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #ffffff;
    background: #24cca7;
    outline: none;
    border: none;
    border-radius: 20px;
    width: 280px;
    height: 50px;
    margin-bottom: 20px;
    transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
}

.ButtonPrimary:hover{
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}

.ButtonSecondary{
    font-family: 'Circe';
    font-weight: 400;
    font-size: 18px;
    line-height: 1.5;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #4a56e2;
    background: #ffffff;
    border: 1px solid #4a56e2;
    border-radius: 20px;
    width: 280px;
    height: 50px;
    transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
}

.ButtonSecondary:hover{
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}

